<template>
    <section class="container">
        <header>
            <img src="../assets/img-banner01-new-be630b3fad5d10f6055ee3c3579e4bed.jpg"/>
            <div class="dashboard-wrap" v-if="chinaTotal">
                <div class="dashboard dashboard-content">
                    <div class="dashboard-content-inner">
                        <div class="dashboard-inner-tab">
                            <div class="dashboard-tab-menu tab-cur">
                                <h3>全国疫情</h3>
                            </div>
                            <div class="dashboard-tab-menu">
                                <h3>
                                    <van-icon name="location-o"/>
                                    <span>疫情</span>
                                </h3>
                            </div>
                        </div>
                        <div class="dashboard-tab-content">
                            <div class="dashboard-list">
                                <div class="dashboard-inner-item">
                                    <p class="red">较昨日+{{addData.confirm}}</p>
                                    <p class="dashboard-item-num red">{{chinaTotal.confirmedCount}}</p>
                                    <p class="dashboard-item-tit">确诊病例</p>
                                </div>
                                <div class="dashboard-inner-item">
                                    <p class="orange">较昨日+{{addData.suspect}}</p>
                                    <p class="dashboard-item-num orange">{{chinaTotal.suspectedCount}}</p>
                                    <p class="dashboard-item-tit">疑似病例</p>
                                </div>
                                <div class="dashboard-inner-item">
                                    <p class="green">较昨日+{{addData.heal}}</p>
                                    <p class="dashboard-item-num green">{{chinaTotal.curedCount}}</p>
                                    <p class="dashboard-item-tit">治愈病例</p>
                                </div>
                                <div class="dashboard-inner-item">
                                    <p class="gray">较昨日+{{addData.dead}}</p>
                                    <p class="dashboard-item-num gray">{{chinaTotal.deadCount}}</p>
                                    <p class="dashboard-item-tit">死亡病例</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </header>
        <section class="tab-container">
            <van-tabs class="re-van-tab" v-model="active" color="#00b9d1" title-active-color="#00b9d1"
                      title-inactive-color="#333" line-width="50px" line-height="5px" sticky animated
                      @rendered="tabRender">
                <van-tab>
                    <div slot="title">
                        <h1 class="re-van-tab-tit">疫情地图</h1>
                    </div>
                    <div class="content-wrapper">
                        <div class="map-title-wrap">
                            <h1 class="map-tit">新型冠状病毒感染的肺炎疫情</h1>
                            <p class="map-sub-tit">截至 2020-02-11 09:51:09 数据统计</p>
                        </div>
                        <div class="map-wrap">
                            <v-chart :options="mapData" autoresize/>
                        </div>
                        <div class="line-content">
                            <div class="line-wrap" :style="{height: lineEchartsHeight}">
                                <v-chart :options="lineData" autoresize/>
                            </div>
                        </div>
                        <div class="line-content">
                            <div class="line-wrap" :style="{height: lineEchartsHeight}">
                                <v-chart :options="line2Data" autoresize/>
                            </div>
                        </div>
                        <div class="line-content">
                            <div class="line-wrap" :style="{height: lineEchartsHeight}">
                                <v-chart :options="line3Data" autoresize/>
                            </div>
                        </div>
                        <div class="line-content">
                            <div class="map-title-wrap">
                                <h1 class="map-tit">中国疫情（包括港澳台）</h1>
                                <p class="map-sub-tit">7:00-10:00为更新高峰，数据如有滞后请谅解。</p>
                            </div>
                            <div class="area-value-header">
                                <van-row type="flex" justify="center">
                                    <van-col span="6"><h3>地区</h3></van-col>
                                    <van-col span="6"><h3>确诊</h3></van-col>
                                    <van-col span="6"><h3>治愈</h3></van-col>
                                    <van-col span="6"><h3>死亡</h3></van-col>
                                </van-row>
                            </div>
                            <van-collapse v-if="areaStat.length > 0" class="area-value-row-wrap" v-model="activeNames">
                                <van-collapse-item v-for="(item, index) in areaStat" v-bind:key="item.id">
                                    <div slot="title">
                                        <van-row type="flex" justify="center">
                                            <van-col span="6"><h3>{{item.provinceShortName}}</h3></van-col>
                                            <van-col span="6">{{item.confirmedCount}}</van-col>
                                            <van-col span="6">{{item.curedCount}}</van-col>
                                            <van-col span="6">{{item.deadCount}}</van-col>
                                        </van-row>
                                    </div>
                                    <van-row class="city-value-wrap" type="flex" justify="center"
                                             v-for="city in item.cities" v-bind:key="city.id">
                                        <van-col span="6" tag="p">{{city.cityName}}</van-col>
                                        <van-col span="6" tag="p">{{city.confirmedCount}}</van-col>
                                        <van-col span="6" tag="p">{{city.curedCount}}</van-col>
                                        <van-col span="6" tag="p">{{city.deadCount}}</van-col>
                                    </van-row>
                                </van-collapse-item>
                            </van-collapse>
                        </div>
                    </div>
                </van-tab>
                <van-tab>
                    <div slot="title">
                        <h1 class="re-van-tab-tit">病情动态</h1>
                    </div>
                    <div class="content-wrapper">
                        <div class="map-title-wrap">
                            <h1 class="map-tit">病情动态</h1>
                            <p class="map-sub-tit">截至 2020-02-11 09:51:09 数据统计</p>
                        </div>
                        <div class="timeline-list">
                            <van-list
                                    v-model="loading"
                                    :finished="finished"
                                    finished-text="没有更多了"
                                    @load="loadMore">
                                <div class="timeline-item" v-for="(item, index) in news.data" v-bind:key="item.id">
                                    <div class="timeline-item-header">
                                        <div v-if="index == 0" class="timeline-item-header-text"><p>
                                            {{formatter(item.pubDate)}}</p>
                                            <van-tag round type="primary">最新</van-tag>
                                        </div>
                                        <div v-else class="timeline-item-header-text"><p>{{formatter(item.pubDate)}}</p>
                                        </div>
                                    </div>
                                    <div class="timeline-item-content">
                                        <h2>{{item.title}}</h2>
                                        <p>{{item.summary}}</p>
                                        <p class="timeline-item-content-from">信息来源：{{item.infoSource}}</p>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </div>
                </van-tab>
                <van-tab>
                    <div slot="title">
                        <h1 class="re-van-tab-tit">谣言辟谣</h1>
                    </div>
                    <div class="content-wrapper">
                        <div class="map-title-wrap">
                            <h1 class="map-tit">谣言辟谣</h1>
                        </div>
                        <div class="card-list">
                            <van-list
                                    v-model="loadingRumours"
                                    :finished="finishedRumours"
                                    finished-text="没有更多了"
                                    @load="loadMoreRumours">
                                <div class="card-item" v-for="(item, index) in rumours.data" v-bind:key="item.id">
                                    <div class="card-num">
                                        <div class="card-num-text">{{index+1}}</div>
                                        <div class="right-div"></div>
                                    </div>
                                    <div v-if="item.rumorType == 0" class="card-item-type"></div>
                                    <div v-if="item.rumorType == 1" class="card-item-type true-type"></div>
                                    <div v-if="item.rumorType == 2" class="card-item-type unknown-type"></div>
                                    <div class="card-title">{{item.title}}</div>
                                    <div class="card-content">
                                        <div class="short-content">{{item.mainSummary}}</div>
                                        <div v-if="item.show" class="long-content">{{item.body}}</div>
                                        <div class="show-btn" @click="showContent(index)">
                                            <span>{{showBtn.name}}详情</span>
                                            <van-icon :name="showBtn.icon" size="14px" color="#555"/>
                                        </div>
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </div>
                </van-tab>
            </van-tabs>
        </section>
        <loading :show="show"></loading>
    </section>
</template>

<script>
    import Vue from 'vue';
    import {Icon, List, Tag, Tab, Tabs, Collapse, CollapseItem, Col, Row} from 'vant';
    import loading from '../components/loading'
    import ECharts from 'vue-echarts' // 在 webpack 环境下指向 components/ECharts.vue
    // 手动引入 ECharts 各模块来减小打包体积
    import 'echarts/lib/chart/line'
    import 'echarts/lib/component/tooltip'
    import 'echarts/lib/component/title'
    import 'echarts/lib/component/legend'
    import 'echarts/lib/chart/map'
    import 'echarts/lib/component/visualMap'
    import chinaMap from 'echarts/map/json/china.json'
    import {getIndexData, getNews, getRumours} from '../service/index'
    import {echartsUtils} from "../utils/echartsUtils";
    import {locationUtils} from "../utils/utils";

    Vue.use(Icon).use(Tab).use(Tag).use(List).use(Tabs).use(Collapse).use(CollapseItem).use(Col).use(Row);
    Vue.component('v-chart', ECharts)
    ECharts.registerMap('china', chinaMap)

    export default {
        name: "index",
        data() {
            return {
                show: true,
                active: 0,
                chinaTotal: null,
                addData: null,
                lineEchartsHeight: '0',
                activeNames: ['1'],
                activeRumous: ['1'],
                areaStat: [],
                loading: false,
                initState: false,
                finished: false,
                loadingRumours: false,
                initStateRumours: false,
                finishedRumours: false,
                news: {
                    data: [],
                    page: 1
                },
                rumours: {
                    data: [],
                    page: 1
                },
                showBtn: {
                  name: '展开',
                  icon: 'arrow-down'
                },
                mapData: echartsUtils.createMapOptions(),
                lineData: echartsUtils.createPolygonalLineOptions('疫情新增趋势', ['#005dff', '#ec9217'], ['新增确诊', '新增疑似']),
                line2Data: echartsUtils.createPolygonalLineOptions('累计确诊/疑似趋势', ['#e83132', '#ec9217'], ['累计确诊', '累计疑似']),
                line3Data: echartsUtils.createPolygonalLineOptions('累计治愈/累计死亡', ['#10aeb5', '#4d5054'], ['累计治愈', '累计死亡']),
            }
        },
        components: {
            loading,
            'v-chart': ECharts
        },
        async mounted() {
            // 1、数据首次加载完后 → 获取图片（或外层框）宽度，并设置其高度
            let ret = await locationUtils.getAddress()

            this.$nextTick(() => {
                // 获取图片（或外层框）
                let imgBox = document.querySelector('.map-wrap')
                // 获取其宽度
                let wImgbox = imgBox.getBoundingClientRect().width
                // 设置其高度（以宽度的60%为例）
                imgBox.style.height = wImgbox + 'px'
                this.lineEchartsHeight = wImgbox / 1.5 + 'px'
                //let imgBox2 = document.querySelector('.line-wrap')
                //imgBox2.style.height = wImgbox / 2 + 'px'
            })
            // 2、挂载 reisze 事件 → 屏幕缩放时监听宽度变化
            window.onresize = () => {
                return (() => {
                    this.$nextTick(() => {
                        let imgBox = document.querySelector('.map-wrap')
                        // 获取其宽度
                        let wImgbox = imgBox.getBoundingClientRect().width
                        // 设置其高度（以宽度的60%为例）
                        imgBox.style.height = wImgbox + 'px'
                        this.lineEchartsHeight = wImgbox / 1.5 + 'px'
                        //let imgBox2 = document.querySelector('.line-wrap')
                        //imgBox2.style.height = wImgbox / 2 + 'px'
                    })
                })()
            }

        },
        methods: {
            async indexInit() {
                let retData = await getIndexData()
                if (retData && retData.data) {
                    let result = retData.data
                    this.chinaTotal = result.allCount
                    this.addData = result.chinaDayAddData[result.chinaDayAddData.length - 1]
                    let data = []
                    let areaStat = []
                    for (let i = 0, len = result.areaStat.length; i < len; i++) {
                        result.areaStat[i].show = false
                        areaStat.push(result.areaStat[i])
                        data.push({
                            name: result.areaStat[i].provinceShortName,
                            value: result.areaStat[i].confirmedCount
                        })
                    }
                    this.mapData.series[0].data = data//echartsUtils.createMapOptions(data)
                    let paseAddData = echartsUtils.paseAddData(result.chinaDayAddData)
                    this.lineData = Object.assign(this.lineData, {
                        series: paseAddData.series,
                        xAxis: {
                            data: paseAddData.xAxisData
                        }
                    })
                    let paseDayData = echartsUtils.paseDayData(result.chinaDayData)
                    this.line2Data = Object.assign(this.line2Data, {
                        series: paseDayData.series,
                        xAxis: {
                            data: paseDayData.xAxisData
                        }
                    })

                    let paseDayHealData = echartsUtils.paseDayHealData(result.chinaDayData)
                    this.line3Data = Object.assign(this.line3Data, {
                        series: paseDayHealData.series,
                        xAxis: {
                            data: paseDayHealData.xAxisData
                        }
                    })
                    this.areaStat = areaStat
                    this.show = false
                }
            },
            async getNewsInit() {
                let retData = await getNews()
                if (retData && retData.data && retData.data.result) {
                    this.news = Object.assign(this.news, {
                        data: this.news.data.concat(retData.data.result),
                        page: this.news.page + 1,
                        //count: retData.count
                    })
                    if (retData.data.total > 20) {
                        this.initState = true
                        this.loading = false
                    } else {
                        this.loading = false
                        this.finished = true
                    }
                } else {
                    this.loading = false
                    this.finished = true
                }
            },
            async getRumoursInit() {
                let retData = await getRumours()
                if (retData && retData.data && retData.data.result) {
                    this.rumours = Object.assign(this.rumours, {
                        data: this.rumours.data.concat(retData.data.result),
                        page: this.rumours.page + 1,
                        //count: retData.count
                    })
                    if (retData.data.total > 10) {
                        this.initStateRumours = true
                        this.loadingRumours = false
                        console.log('initStateRumours', this.initStateRumours)
                        console.log('loadingRumours', this.loadingRumours)
                    } else {
                        this.loadingRumours = false
                        this.finishedRumours = true
                    }
                } else {
                    this.loadingRumours = false
                    this.finishedRumours = true
                    console.log('initStateRumours2', this.initStateRumours)
                    console.log('loadingRumours2', this.loadingRumours)
                }
            },
            async tabRender(name, title) {
                switch (name) {
                    case 0:
                        await this.indexInit()
                        break
                    case 1:
                        setTimeout(async () => {
                            await this.getNewsInit()
                        }, 500)
                        break
                    case 2:
                        setTimeout(async () => {
                            await this.getRumoursInit()
                        }, 500)
                        break
                }
            },
            formatter(timestamp) {
                if (timestamp) {
                    let date = new Date(timestamp)
                    let month = date.getMonth() + 1,
                        day = date.getDate(),
                        hour = date.getHours(),
                        minute = date.getMinutes(),
                        second = date.getSeconds()

                    hour = hour > 9 ? hour : '0' + hour
                    minute = minute > 9 ? minute : '0' + minute

                    return `${month}-${day} ${hour}:${minute}`
                }
                return ''
            },
            async loadMore() {
                console.log('initState', this.initState)
                console.log('loadMore loadMore loadMore')
                if (!this.initState) {
                    return
                }
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                let retData = await getNews(this.news.page)
                if (retData && retData.data && retData.data.result) {
                    this.news = Object.assign(this.news, {
                        data: this.news.data.concat(retData.data.result),
                        page: this.news.page + 1,
                        //count: retData.count
                    })
                    this.loading = false
                } else {
                    this.finished = true
                }
            },
            async loadMoreRumours() {
                console.log('loadMoreRumours loadMoreRumours loadMoreRumours')
                console.log('initStateRumours', this.initStateRumours)
                console.log('loadingRumours', this.loadingRumours)
                if (!this.initStateRumours) {
                    return
                }
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                let retData = await getRumours(this.rumours.page)
                if (retData && retData.data && retData.data.result) {
                    this.rumours = Object.assign(this.rumours, {
                        data: this.rumours.data.concat(retData.data.result),
                        page: this.rumours.page + 1,
                        //count: retData.count
                    })
                    this.loadingRumours = false
                } else {
                    this.finishedRumours = true
                }
            },
            showContent(index) {
                this.$set(this.rumours.data[index], 'show', !this.rumours.data[index]['show'])
                this.showBtn = Object.assign(this.showBtn, {
                    name: this.showBtn.icon === 'arrow-down' ? '收起' : '展开',
                    icon: this.showBtn.icon === 'arrow-down' ? 'arrow-up' : 'arrow-down'
                })
                //this.rumours.data[index]['show'] = true//!this.rumours.data[index]['show']

            }
        }
    }
</script>
<style>
    .van-cell--clickable {
        padding-left: 0;
        padding-right: 0;
    }

    .van-cell__right-icon {
        position: absolute;
        right: 0;
    }

    .area-value-row-wrap .van-collapse-item__content {
        padding-left: 0;
        padding-right: 0;
    }

    .card-item .van-cell__title {
        text-align: right;
        padding-right: 20px;
    }

    .card-item .van-hairline--top-bottom:after {
        content: none !important;
    }

    .card-item .van-cell:not(:last-child)::after {
        content: none !important;
    }

    .card-item .van-cell {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
    }
</style>
<style scoped>
    .echarts {
        width: 100%;
        height: 100%;
    }

    .re-van-tab-tit {
        font-size: 16px;
        font-weight: 600;
    }

    .van-tab--active .re-van-tab-tit {
        font-weight: 700;
    }

    .container {
        background: #f5f5f5;
        font-family: -apple-system-font, Helvetica Neue, sans-serif;
    }

    header {
        position: relative;
        width: 100%;
        height: 232px;
        z-index: 2;
        overflow: hidden;
        background-color: #fff;
    }

    header img {
        width: 100%;
        height: auto;
        object-fit: cover;
        object-position: 0 0;
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
    }

    .dashboard-wrap {
        position: absolute;
        overflow: hidden;
        width: 100%;
        bottom: 18px;
        z-index: 2;
    }

    .dashboard {
        margin: 0 14px;
        border-radius: 10px;
        position: relative;
        z-index: 2;
    }

    .dashboard-inner-tab {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        background-image: linear-gradient(to right, #0f8ebf, #6bdaff, #0f8ebf);
    }

    .dashboard-tab-menu {
        height: 40px;
        line-height: 40px;
        width: 50%;
        display: inline-block;
        vertical-align: middle;
        text-align: center;
        box-sizing: border-box;
        position: relative;
        z-index: 2;
        color: #fff;
    }

    .dashboard-tab-menu:after {
        content: "";
        height: 40px;
        background-image: url("../assets/tab-bg-85e3ffbe8f7b11ab99acb92052f495fe.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-position: 0 0;
        position: absolute;
        left: -15px;
        top: 0;
        right: 0;
        z-index: -1;
        pointer-events: none;
        display: none;
    }

    .dashboard-tab-menu h3 {
        font-size: 17px;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .dashboard-tab-menu.tab-cur:after {
        display: block;
    }

    .dashboard-tab-menu.tab-cur {
        color: #333;
    }

    .dashboard-tab-menu:first-child::after {
        transform: rotateY(180deg);
        right: -15px;
        left: 0;
    }

    .dashboard-content-inner {
        position: relative;
        border-radius: 10px;
    }

    .dashboard-tab-content {
        height: 90px;
        background-color: #fff;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        padding: 0;
    }

    .dashboard-list {
        display: flex;
        height: 90px;
        border-radius: 10px;
    }

    .dashboard-list .dashboard-inner-item {
        width: 25%;
        flex: 1;
        position: relative;
        z-index: 2;
        padding-top: 15px;
    }

    .dashboard-list .dashboard-inner-item p {
        z-index: 2;
        position: relative;
    }

    .dashboard-item-num {
        font-size: 20px;
        font-weight: 500;
        margin: 5px 0 3px 0;
    }

    .dashboard-item-tit {
        font-weight: 500;
        color: #333;
    }

    .dashboard-inner-item:first-child:after {
        right: 1px;
        opacity: .05;
        background-image: linear-gradient(180deg, rgba(199, 33, 28, 0), #e5453c);
    }

    .dashboard-inner-item:after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 60px;
        z-index: 1;
        pointer-events: none;
    }

    .dashboard-inner-item:nth-child(2):after {
        right: 1px;
        opacity: .11;
        background-image: linear-gradient(180deg, rgba(255, 163, 23, 0), #ffd033);
    }

    .dashboard-inner-item:nth-child(3):after {
        right: 1px;
        opacity: .06;
        background-image: linear-gradient(180deg, rgba(23, 144, 91, 0), #33c394);
    }

    .dashboard-inner-item:nth-child(4):after {
        background-image: linear-gradient(180deg, rgba(241, 242, 244, 0), #f9fafa);
        opacity: .06;
    }

    .red {
        color: #de5e5b;
    }

    .orange {
        color: #e2941d;
    }

    .green {
        color: #62c298;
    }

    .gray {
        color: #888;
    }

    .content-wrapper {
        padding: 20px;
        position: relative;
        background-color: #fff;
    }

    .map-title-wrap {

    }

    .map-title-wrap .map-tit {
        position: relative;
        font-size: 17px;
        color: #333;
        line-height: 25px;
        margin-bottom: 8px;
        padding-left: 12px;
        font-weight: 500;
        overflow: hidden;
