import axios from 'axios'
import {baseUrl,mapKey,mapUrl} from "../config";

export const getIndexData = async () => {
    return await axios.get(`${baseUrl}/getData?action=getInfo`)
}

export const getNews = async (pageNum = 1, limit = 20) => {
    return await axios.get(`${baseUrl}/getData?action=getNews&pageNum=${pageNum}&limit=${limit}`)
}

export const getRumours = async (pageNum = 1, limit = 10) => {
    return await axios.get(`${baseUrl}/getData?action=getRumours&pageNum=${pageNum}&limit=${limit}`)
}

export const getFromMapUrlPosition = async (latitude = 23, longitude = 113) => {
    return await axios.get(`${mapUrl}?key=${mapKey}&latitude=${latitude}&longitude=${longitude}`)
}

export const getPositionByIp = async () => {
    //http://service-3zzp33ya-1251703291.gz.apigw.tencentcs.com/release/ip
    return await axios.get(`${baseUrl}/getData?action=getPositionByIP`)
}
