import {getFromMapUrlPosition,getPositionByIp} from "../service";

export const locationUtils = {
    getPosition: () => {
        return new Promise((resolve, reject) => {
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(function (position) {
                    let latitude = position.coords.latitude
                    let longitude = position.coords.longitude
                    let data = {
                        latitude: latitude,
                        longitude: longitude
                    }
                    resolve(data)
                }, function () {
                    reject(arguments)
                },{
                    //enableHighAcuracy: true,
                    // 指定获取地理位置的超时时间，默认不限时，单位为毫秒
                    timeout: 5000,
                    // 最长有效期，在重复获取地理位置时，此参数指定多久再次获取位置。
                })
            } else {
                reject('你的浏览器不支持当前地理位置信息获取')
            }
        })
    },
    getAddress: async () => {
        try{
            let position = await locationUtils.getPosition()
            console.log('position',position)
            if(position) {
                let ret = await getFromMapUrlPosition(position.latitude, position.longitude)
                alert(ret)
            }else{

            }
        } catch (err) {
            console.log('err',err)
        }
        return null
    }

}
